import { BasePlugin } from '../base';
import 'intersection-observer';
interface Options extends IntersectionObserverInit {
    max: number;
    immediately: false;
}
/**
 * 元素曝光插件
 */
export default class AutoShow extends BasePlugin {
    static override key = 'autoShow';
    private max: number;
    private observer?: IntersectionObserver;
    private observerOptions: IntersectionObserverInit;
    private elementsNum: number;
    private immediately: boolean;
    constructor(options?: Options) {
        super();
        // 默认值
        options = {
            max: 50,
            root: null,
            rootMargin: '0px',
            threshold: 0.3,
            immediately: false,
            ...options,
        };
        this.max = options.max;
        this.elementsNum = 0;
        this.observerOptions = {
            root: options.root || null,
            rootMargin: options.rootMargin || '0px',
            threshold: options.threshold || 0.3,
        };
        this.immediately = options.immediately;
    }
    private init() {
        this.observer = new IntersectionObserver(
            (entries: IntersectionObserverEntry[]) => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) {
                        this.log(entry.target as HTMLElement);
                        this.remove(entry.target as HTMLElement);
                    }
                });
            },
            this.observerOptions,
        );
    }

    add(entry: HTMLElement) {
        if (this.observer) {
            if (this.elementsNum < this.max) {
                this.observer.observe(entry);
                this.elementsNum++;
            }
        } else {
            this.init();
            this.add(entry);
        }
    }

    remove(element: HTMLElement) {
        this.observer?.unobserve(element);
        this.elementsNum--;
    }
    removeAll() {
        this.destroy();
    }
    destroy() {
        this.observer?.disconnect() && (this.elementsNum = 0);
    }

    private getValue(element: HTMLElement) {
        const params = element.dataset.showLogParams || "";
        const action = element.dataset.showLogAction;
        if (action) {
            return {
                action,
                params: JSON.parse(params),
            };
        }
    }
    private log(element: HTMLElement) {
        const value = this.getValue(element);
        if (!this.weblog || !value) return;
        this.weblog[this.immediately ? 'sendImmediately' : 'collect'](
            'SHOW',
            // @ts-ignore
            value
        );
    }
}
